body {
	min-width: 768px;
	background: #FFF;
	@include f("psr",20,26);
	color: $black;
	-webkit-font-smoothing: subpixel-antialiased; }

// layout
.container {
	max-width: 1710px;
	margin: 0 auto;
	padding: 0 15px;
	@include sm {
		padding: 0 10px; } }

.out {
	overflow: hidden; }

// titles
h1, h2, h3, h4, h5, h6 {
	font-weight: normal; }

.title-xs {
	@include f("oss",19,30);
	color: $black; }

.title-sm {
	@include f("psb",20); }

.title-md {
	font-family: Arial, sans-serif;
	font-size: 20px;
	line-height: 1.3; }

.title-lg {
	color: #274F7F;
	@include f('psr',27);
	text-transform: uppercase; }

// basic transition
@mixin trs {
	transition: .2s cubic-bezier(0.55, 0.09, 0.68, 0.53); }

// links
.link-decor {
	padding: 3px;
	color: #008CFF;
	background-image: linear-gradient(to right, #008CFF 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, #008CFF 16%, rgba(255, 255, 255, 0) 0%);
	background-size: 22px 2px, 6px 2px;
	background-repeat: repeat-x;
	background-position: bottom;
	&:hover {
		color: darken(#008CFF, 10);
		background-image: linear-gradient(to right, darken(#008CFF, 10) 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, darken(#008CFF, 10) 16%, rgba(255, 255, 255, 0) 0%); } }

// mini reset
a {
	text-decoration: none;
	color: inherit; }

img {
	display: block; }

button {
	background: none;
	@include f('psr'); }

// buttons
.btn {
	position: relative;
	display: inline-block;
	min-width: 340px;
	padding: 16px 70px 16px 5px;
	@include f("psr",22);
	color: $white;
	outline: none;
	text-decoration: none;
	text-align: center;
	background-color: #52C0E9;
	border-bottom: 1px solid #E7E7E7;
	@include lg {
		min-width: 280px;
		font-size: 20px;
		padding: 13px 55px 13px 5px; }
	@include md {
		min-width: 238px;
		padding: 11px 41px 11px 5px;
		font-size: 17px; }
	@include sm {
		min-width: 210px;
		padding: 6px 34px 6px 5px;
		font-size: 15px; }
	&::after {
		content: '';
		position: absolute;
		top: calc(50% - 18px);
		right: 32px;
		z-index: 1;
		@include s('btn_arr');
		transform: scale(.9);
		@include lg {
			right: 13px; }
		@include md {
			right: 10px;
			transform: scale(.7); }
		@include sm {
			right: 6px;
			transform: scale(.6); } }
	&:hover {
		background-color: #128FCD;
		border-bottom-color: #F9BF84; }
	&:hover::after {
		@include s('btn_arr-hvr'); }
	&:active {
		background-color: #24568D;
		border-bottom-color: #E63833; }
	&:active::after {
		@include s('btn_arr-act'); }
	&_dark {
		background-color: #1768BF;
		border-bottom-color: #F9BF84;
		&::after {
			@include s('btn_dark'); }
		&:hover {
			background-color: #00BBFF;
			border-bottom-color: #F9BF84; }
		&:hover::after {
			@include s('btn_dark-hvr'); }
		&:active {
			background-color: #24568D;
			border-bottom-color: #E63833; } } }

.send-btn {
	position: relative;
	padding: 0 70px 0 5px;
	min-width: 232px;
	line-height: 45px;
	height: 45px;
	color: #fff;
	background: #6ca5db;
	text-align: right;
	border-bottom: 2px solid #a7b8c9;
	@include f("ver",19);
	overflow: hidden;
	&::after {
		position: absolute;
		top: -4px;
		right: -4px;
		@include s('send');
		content: '';
		transform: scale(.87);
		@include md {
			top: -7px;
			right: -8px;
			transform: scale(.73); } }
	&:hover {
		background-color: #63afed;
		border-bottom-color: #ffff74; }
	&:hover::after {
		@include s('send-hvr'); }
	&:active {
		background-color: #415c89;
		border-bottom-color: #a95a63; }
	&:active::after {
		@include s('send-act'); } }
